<template>
  <q-page class="q-pa-md no-scroll">
    <!-- Карточка игрока -->
    <q-card class="player-card">
      <div class="q-gutter-sm row justify-between items-left">
        <div class="nickname row items-center">
          <q-chip square color="primary" text-color="white" icon="account_circle">
            {{ userId }}
          </q-chip>
          <q-chip square color="orange" text-color="white" icon-right="star">
            {{ lvl }}
          </q-chip>
          <q-chip square color="red" text-color="white">
            {{ points }}
          </q-chip>
        </div>
        <q-knob
          v-model="counter"
          size="50px"
          :thickness="0.5"
          :color="knobColor"
          center-color="orange-10"
          show-value
          readonly
          class="knob-style"
          @click="handleClick"
        />
      </div>
    </q-card>
    <q-card class="player-card">
      <div class="q-gutter-sm row justify-between items-left">
        <q-list class="list-container">
        <q-item v-for="(user, index) in topUsers" :key="index" dense>
          <q-item-section>
            <q-item-label>
              {{ index + 1 }}. {{ user.id }} — {{ user.clicks }} кликов
            </q-item-label>
          </q-item-section>
        </q-item>
      </q-list>
      </div>
    </q-card>

    <!-- Топ пользователей -->
    <div class="tops">
    </div>

    <!-- Центральная кнопка -->
    <div class="flex-center">
      <q-btn
        round
        :color="knobColor"
        size="xl"
        @click="handleClick"
        class="click-button"
      >
        <q-spinner-rings color="white" />
        <q-tooltip>Жми!</q-tooltip>
      </q-btn>
    </div>
  </q-page>
</template>

<script>
import { io } from "socket.io-client";

export default {
  name: "AppBody",
  data() {
    return {
      telega:window.Telegram.WebApp,
      points: 0,
      counter: 0, // Счётчик нажатий
      lvl: 0,
      audio: null, // Объект аудио
      timeoutId: null, // ID таймера
      isAudioPlaying: false, // Флаг для отслеживания состояния звука
      topUsers: [],
      socket: null,
      userId: '',
      sounds: Array.from({ length: 72 }, (_, i) => `${i + 1}.mp3`), // Массив с названиями треков
      chosenSounds: [] // Массив для хранения уже выбранных звуков
    };
  },
  computed: {
    knobColor() {
      const colors = ["blue", "green", "orange", "red"];
      return colors[(this.lvl - 1) % colors.length] || "gray";
    },
    
  },
  mounted() {
    this.audio = new Audio(`/sounds/${this.getRandomSound()}`);
    this.audio.loop = true;
    this.socket = io("https://1223191-ce40147.tw1.ru");

    // Слушаем обновления топа
    this.socket.on("updateTop", ({ topUsers }) => {
      this.topUsers = topUsers;
    });

    // Отправляем начальные данные
    this.sendData();

    //sdfsd/fds/f/s/f/dsf/sd/f/
    this.telega = window.Telegram.WebApp;
  this.telega.ready();  // Убедитесь, что WebApp готов

  // Печатаем initData в консоль для понимания формата
  console.log(this.telega.initData);

  // Если initData - это строка запроса
  const queryParams = new URLSearchParams(this.telega.initData);

  // Преобразуем строку запроса в объект
  const userData = {};
  queryParams.forEach((value, key) => {
    userData[key] = value;
  });
  
  // Логируем полученные данные
  console.log(userData);
  const userName = JSON.parse(userData.user);
  this.userId = userName.first_name
  // Пример: выводим ID пользователя
  console.log("NAME пользователя:", userName);  // Пример поля

  },
  methods: {
    getRandomSound() {
      if (this.sounds.length === 0) {
        alert("Все звуки использованы!");
        return null;
      }

      const randomIndex = Math.floor(Math.random() * this.sounds.length);
      const chosenSound = this.sounds[randomIndex];

      // Удаляем выбранный звук из массива
      this.sounds.splice(randomIndex, 1);

      // Сохраняем выбранный звук (если нужно)
      this.chosenSounds.push(chosenSound);

      return chosenSound;
    },
    getRandomArbitrary(min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
    sendData() {
      this.socket.emit("click", {
        userId: this.userId,
        clicks: this.points,
        level: this.lvl,
      });
    },
    handleClick() {
      this.counter++;
      this.points = this.lvl * 100 + this.counter;
      this.sendData();

      // Повышение уровня при каждом 100-м нажатии
      if (Math.floor(this.counter / 100) > 0) {
        this.lvlUp();
      }

      // Проигрываем звук при клике
      if (!this.isAudioPlaying) {
        this.audio.play();
        this.isAudioPlaying = true;
      }

      // Сбрасываем таймер для остановки звука
      clearTimeout(this.timeoutId);
      this.timeoutId = setTimeout(() => {
        this.stopAudio();
      }, 300);
    },
    stopAudio() {
      this.audio.pause();
      this.audio.currentTime = 0;
      this.isAudioPlaying = false;
      this.counter = 0;
    },
    lvlUp() {
      this.lvl++;
      this.counter = 0;
      this.stopAudio();

      // Меняем звук на новый при повышении уровня
      this.audio = new Audio(`/sounds/${this.getRandomSound()}`);
      this.audio.loop = true;
    },
  },
};
</script>

